import React from 'react';
import { Layout, Button, Typography, Space, Tooltip } from 'antd';
import { 
  SafetyOutlined, 
  ClearOutlined,
  InfoCircleOutlined,
  GithubOutlined,
  BulbOutlined
} from '@ant-design/icons';
import { HeaderProps } from '../types';
import './Header.css';

const { Header: AntHeader } = Layout;
const { Title, Text } = Typography;

const Header: React.FC<HeaderProps> = ({ hasData, onClear }) => {
  return (
    <AntHeader className="app-header">
      <div className="header-container">
        {/*  */}
        <div className="header-brand">
          <div className="brand-icon">
            <SafetyOutlined />
          </div>
          <div className="brand-info">
            <Title level={3} className="brand-title">
              
            </Title>
            <Text className="brand-subtitle">
              AI
            </Text>
          </div>
        </div>

        {/*  */}
        <div className="header-features">
          <Space size="large">
            <div className="feature-item">
              <BulbOutlined className="feature-icon" />
              <span></span>
            </div>
            <div className="feature-item">
              <SafetyOutlined className="feature-icon" />
              <span></span>
            </div>
            <div className="feature-item">
              <InfoCircleOutlined className="feature-icon" />
              <span></span>
            </div>
          </Space>
        </div>

        {/*  */}
        <div className="header-actions">
          <Space>
            {/*  */}
            <Tooltip title="">
              <Button 
                type="text" 
                icon={<InfoCircleOutlined />}
                className="header-button"
                onClick={() => {
                  // TODO: 
                  console.log('');
                }}
              >
                
              </Button>
            </Tooltip>

            {/* GitHub */}
            <Tooltip title="">
              <Button 
                type="text" 
                icon={<GithubOutlined />}
                className="header-button"
                onClick={() => {
                  window.open('https://github.com', '_blank');
                }}
              >
                
              </Button>
            </Tooltip>

            {/*  */}
            {hasData && (
              <Tooltip title="">
                <Button 
                  type="primary" 
                  danger
                  icon={<ClearOutlined />}
                  className="clear-button"
                  onClick={onClear}
                >
                  
                </Button>
              </Tooltip>
            )}
          </Space>
        </div>
      </div>

      {/*  */}
      <div className="header-description">
        <Text className="description-text">
          AI
        </Text>
        <div className="description-stats">
          <Space split={<span className="stats-divider"></span>}>
            <span> JPGPNGBMPWebP </span>
            <span> 20MB </span>
            <span> AI </span>
            <span></span>
          </Space>
        </div>
      </div>
    </AntHeader>
  );
};

export default Header;
