import React, { useState, useCallback } from 'react';
import { Card, Upload, Button, Space, Typography, Image, Progress, Alert, message } from 'antd';
import { 
  CloudUploadOutlined, 
  EyeOutlined, 
  ReloadOutlined,
  ScanOutlined,
  FileImageOutlined,
  InfoCircleOutlined
} from '@ant-design/icons';
import type { UploadFile, UploadProps } from 'antd/es/upload/interface';
import { UploadSectionProps } from '../types';
import { uploadImage, analyzeImage } from '../services/api';
import './UploadSection.css';

const { Title, Text, Paragraph } = Typography;
const { Dragger } = Upload;

const UploadSection: React.FC<UploadSectionProps> = ({
  uploadedFile,
  analysisData,
  isAnalyzing,
  onFileUploaded,
  onAnalysisStart,
  onAnalysisComplete,
  onAnalysisError,
  onReAnalyze
}) => {
  const [uploadProgress, setUploadProgress] = useState<number>(0);
  const [isUploading, setIsUploading] = useState<boolean>(false);
  const [previewImage, setPreviewImage] = useState<string>('');
  const [previewVisible, setPreviewVisible] = useState<boolean>(false);

  // 
  const beforeUpload = useCallback((file: File) => {
    // 
    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/bmp', 'image/webp'];
    if (!allowedTypes.includes(file.type)) {
      message.error(' JPGPNGBMPWebP ');
      return false;
    }

    // 20MB
    const maxSize = 20 * 1024 * 1024;
    if (file.size > maxSize) {
      message.error(' 20MB');
      return false;
    }

    // 
    return new Promise<boolean>((resolve) => {
      const img = new window.Image();
      img.onload = () => {
        if (img.width < 480 || img.height < 360) {
          message.warning(' 480x360 ');
        }
        resolve(true);
      };
      img.onerror = () => {
        message.error('');
        resolve(false);
      };
      img.src = URL.createObjectURL(file);
    });
  }, []);

  // 
  const customUpload = useCallback(async (options: any) => {
    const { file, onProgress, onSuccess, onError } = options;
    
    try {
      setIsUploading(true);
      setUploadProgress(0);

      // FormData
      const formData = new FormData();
      formData.append('image', file);

      // 
      const progressInterval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 90) {
            clearInterval(progressInterval);
            return prev;
          }
          return prev + Math.random() * 10;
        });
      }, 100);

      // API
      const response = await uploadImage(formData, (progressEvent) => {
        if (progressEvent.total) {
          const percent = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          setUploadProgress(percent);
          onProgress({ percent });
        }
      });

      clearInterval(progressInterval);
      setUploadProgress(100);

      if (response.success && response.data) {
        onSuccess(response.data);
        onFileUploaded(response.data);
        message.success('');
      } else {
        throw new Error(response.message || '');
      }
    } catch (error: any) {
      console.error(':', error);
      onError(error);
      message.error(`: ${error.message || ''}`);
    } finally {
      setIsUploading(false);
      setUploadProgress(0);
    }
  }, [onFileUploaded]);

  // 
  const handleStartAnalysis = useCallback(async () => {
    if (!uploadedFile) {
      message.warning('');
      return;
    }

    try {
      onAnalysisStart();
      
      const response = await analyzeImage({
        imageUrl: uploadedFile.url,
        imagePath: uploadedFile.path
      });

      if (response.success && response.data) {
        onAnalysisComplete(response.data);
      } else {
        throw new Error(response.message || '');
      }
    } catch (error: any) {
      console.error(':', error);
      onAnalysisError(error.message || '');
    }
  }, [uploadedFile, onAnalysisStart, onAnalysisComplete, onAnalysisError]);

  // 
  const handlePreview = useCallback(() => {
    if (uploadedFile?.url) {
      setPreviewImage(uploadedFile.url);
      setPreviewVisible(true);
    }
  }, [uploadedFile]);

  // 
  const uploadProps: UploadProps = {
    name: 'image',
    multiple: false,
    showUploadList: false,
    beforeUpload,
    customRequest: customUpload,
    accept: 'image/*',
  };

  return (
    <div className="upload-section">
      <Card className="upload-card" variant="borderless">
        <div className="upload-header">
          <Title level={2} className="upload-title">
            <FileImageOutlined className="title-icon" />
            
          </Title>
          <Paragraph className="upload-description">
            AI
          </Paragraph>
        </div>

        {!uploadedFile ? (
          // 
          <div className="upload-area">
            <Dragger {...uploadProps} className="upload-dragger">
              <div className="upload-content">
                <div className="upload-icon">
                  <CloudUploadOutlined />
                </div>
                <Title level={4} className="upload-text">
                  
                </Title>
                <Text className="upload-hint">
                   JPGPNGBMPWebP  20MB
                </Text>
                <div className="upload-features">
                  <Space size="large">
                    <div className="feature-tag">
                      <InfoCircleOutlined /> 
                    </div>
                    <div className="feature-tag">
                      <ScanOutlined /> 
                    </div>
                    <div className="feature-tag">
                      <EyeOutlined /> 
                    </div>
                  </Space>
                </div>
              </div>
            </Dragger>

            {/*  */}
            {isUploading && (
              <div className="upload-progress">
                <Progress 
                  percent={uploadProgress} 
                  status="active"
                  strokeColor="#1890ff"
                />
                <Text className="progress-text">...</Text>
              </div>
            )}

            {/*  */}
            <Alert
              message=""
              description={
                <ul className="upload-tips">
                  <li> 480x360</li>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              }
              type="info"
              showIcon
              className="upload-tips-alert"
            />
          </div>
        ) : (
          // 
          <div className="uploaded-file">
            <div className="file-preview">
              <div className="preview-image">
                <img 
                  src={uploadedFile?.url || ''} 
                  alt={uploadedFile?.originalName || ''}
                  className="uploaded-image"
                />
                <div className="preview-overlay">
                  <Button 
                    type="primary" 
                    icon={<EyeOutlined />}
                    onClick={handlePreview}
                    className="preview-button"
                  >
                    
                  </Button>
                </div>
              </div>
              
              <div className="file-info">
                <Title level={4} className="file-name">
                  {uploadedFile?.originalName || ''}
                </Title>
                <div className="file-details">
                  <Space direction="vertical" size="small">
                    <Text>
                      <strong>:</strong> {uploadedFile?.imageInfo?.width || 0}  {uploadedFile?.imageInfo?.height || 0}
                    </Text>
                    <Text>
                      <strong>:</strong> {((uploadedFile?.size || 0) / 1024 / 1024).toFixed(2)} MB
                    </Text>
                    <Text>
                      <strong>:</strong> {uploadedFile?.imageInfo?.format?.toUpperCase() || ''}
                    </Text>
                    <Text>
                      <strong>:</strong> {uploadedFile?.uploadTime ? new Date(uploadedFile.uploadTime).toLocaleString() : ''}
                    </Text>
                  </Space>
                </div>
              </div>
            </div>

            {/*  */}
            <div className="file-actions">
              <Space size="large">
                {!analysisData && !isAnalyzing && (
                  <Button 
                    type="primary" 
                    size="large"
                    icon={<ScanOutlined />}
                    onClick={handleStartAnalysis}
                    className="analyze-button"
                  >
                    AI
                  </Button>
                )}
                
                {analysisData && (
                  <Button 
                    type="default" 
                    size="large"
                    icon={<ReloadOutlined />}
                    onClick={onReAnalyze}
                    className="reanalyze-button"
                  >
                    
                  </Button>
                )}

                <Button 
                  type="default" 
                  size="large"
                  icon={<CloudUploadOutlined />}
                  onClick={() => {
                    window.location.reload();
                  }}
                  className="upload-new-button"
                >
                  
                </Button>
              </Space>
            </div>

            {/*  */}
            {isAnalyzing && (
              <div className="analysis-status">
                <Alert
                  message="..."
                  description="AI"
                  type="info"
                  showIcon
                  className="analysis-alert"
                />
              </div>
            )}
          </div>
        )}
      </Card>

      {/*  */}
      <Image
        width={200}
        style={{ display: 'none' }}
        src={previewImage}
        preview={{
          visible: previewVisible,
          src: previewImage,
          onVisibleChange: (visible) => setPreviewVisible(visible),
        }}
      />
    </div>
  );
};

export default UploadSection;
