import React, { useState, useEffect } from 'react';
import { Layout, message, FloatButton } from 'antd';
import { ArrowUpOutlined } from '@ant-design/icons';
import Header from './components/Header';
import UploadSection from './components/UploadSection';
import AnalysisResult from './components/AnalysisResult';
import LoadingOverlay from './components/LoadingOverlay';
import SimpleErrorBoundary from './components/SimpleErrorBoundary';
import { AnalysisData, UploadedFile } from './types';
import './App.css';

const { Content, Footer } = Layout;

const App: React.FC = () => {
  const [uploadedFile, setUploadedFile] = useState<UploadedFile | null>(null);
  const [analysisData, setAnalysisData] = useState<AnalysisData | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState<boolean>(false);
  const [analysisHistory, setAnalysisHistory] = useState<AnalysisData[]>([]);

  // 
  useEffect(() => {
    message.config({
      top: 100,
      duration: 3,
      maxCount: 3,
    });
  }, []);

  // 
  const handleFileUploaded = (file: UploadedFile) => {
    try {
      // 
      if (!file || !file.url || !file.originalName) {
        message.error('');
        return;
      }
      
      setUploadedFile(file);
      setAnalysisData(null); // 
      message.success('');
    } catch (error) {
      console.error(':', error);
      message.error('');
    }
  };

  // 
  const handleAnalysisComplete = (data: AnalysisData) => {
    setAnalysisData(data);
    setIsAnalyzing(false);
    
    // 
    setAnalysisHistory(prev => [data, ...prev.slice(0, 9)]); // 10
    
    const { violations } = data.analysis;
    const severeCount = violations.filter(v => v.type === '').length;
    const normalCount = violations.filter(v => v.type === '').length;
    
    if (severeCount > 0) {
      message.warning(` ${severeCount}  ${normalCount} `);
    } else if (normalCount > 0) {
      message.info(` ${normalCount} `);
    } else {
      message.success('');
    }
  };

  // 
  const handleAnalysisStart = () => {
    setIsAnalyzing(true);
    message.loading('AI...', 0);
  };

  // 
  const handleAnalysisError = (error: string) => {
    setIsAnalyzing(false);
    message.destroy(); // loading
    message.error(`${error}`);
  };

  // 
  const handleReAnalyze = () => {
    if (uploadedFile) {
      setAnalysisData(null);
    }
  };

  // 
  const handleClear = () => {
    setUploadedFile(null);
    setAnalysisData(null);
    setIsAnalyzing(false);
    message.destroy();
    message.info('');
  };

  return (
    <SimpleErrorBoundary>
      <Layout className="app-layout">
        {/*  */}
        <Header 
          hasData={!!(uploadedFile || analysisData)}
          onClear={handleClear}
        />
        
        {/*  */}
        <Content className="app-content">
          <div className="content-container">
            {/*  */}
            <UploadSection
              uploadedFile={uploadedFile}
              analysisData={analysisData}
              isAnalyzing={isAnalyzing}
              onFileUploaded={handleFileUploaded}
              onAnalysisStart={handleAnalysisStart}
              onAnalysisComplete={handleAnalysisComplete}
              onAnalysisError={handleAnalysisError}
              onReAnalyze={handleReAnalyze}
            />
            
            {/*  */}
            {analysisData && (
              <AnalysisResult
                data={analysisData}
                onReAnalyze={handleReAnalyze}
              />
            )}
          </div>
        </Content>
        
        {/*  */}
        <Footer className="app-footer">
          <div className="footer-content">
            <div className="footer-info">
              <p> v1.0.1 &copy; 2024 - AI</p>
              <p className="footer-description">
                  AI    
              </p>
            </div>
            <div className="footer-stats">
              {analysisHistory.length > 0 && (
                <span className="analysis-count">
                   {analysisHistory.length} 
                </span>
              )}
            </div>
          </div>
        </Footer>
        
        {/*  */}
        <LoadingOverlay visible={isAnalyzing} />
        
        {/*  */}
        <FloatButton.BackTop
          className="back-top-button"
          icon={<ArrowUpOutlined />}
        />
      </Layout>
    </SimpleErrorBoundary>
  );
};

export default App;
